
@font-face {
  font-family: "icon";
  /* project id 172436 */
  src: url("//at.alicdn.com/t/font_w71lovnj7adobt9.eot");
  src: url("//at.alicdn.com/t/font_w71lovnj7adobt9.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_w71lovnj7adobt9.woff") format("woff"), url("//at.alicdn.com/t/font_w71lovnj7adobt9.ttf") format("truetype"), url("//at.alicdn.com/t/font_w71lovnj7adobt9.svg#iconfont") format("svg");
}
.icon {
  font-family: "icon" !important;
  font-size: 18px;
  font-style: normal;
  color: #ffffff;
}
html,
body {
  height: 100%;
}
a.active {
  text-decoration: none;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  background: #f5f5f5;
}
#app .content {
    padding-top: 1.33333rem;
    background: #f5f5f5;
}
#app .tabar {
    margin-bottom: 1.6rem;
}
#app .slide-left-enter-active,
  #app .slide-left-leave-active {
    transition: all 0.1s ease-in;
    opacity: 1;
}
#app .slide-left-enter,
  #app .slide-left-leave-active {
    opacity: 0;
}
@keyframes slideLeft {
from {
    transform: translate3d(100%, 0, 0);
    visibility: visible;
}
to {
    transform: translate3d(0, 0, 0);
}
}



